<template>
    <div>
        <el-row>
            <el-col class="search-bar">
                <el-input placeholder="请输入订单号" class="w_200" v-model="searchOrder"></el-input>
                <div class="m_l_20">
                    <el-input placeholder="请输入用户" class="w_200" v-model="searchName"></el-input>
                </div>
                <div class="m_l_20">
                    <el-button @click="search" icon="search">搜索</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="sortTable">{{orderBy == 1? '正': '倒'}}序排列</el-button>
                </div>
                <div class="m_l_20">
                    <el-button @click="exportExc" :loading="exportLoading" icon='document'>导出Excel</el-button>
                </div>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <el-table v-loading="listLoading" class="table" :data="tableData" border width="100%" :max-height="tableMaxHeight">
                    <el-table-column align="center" label="序号" width="80">
                        <template scope="scope">
                            {{(scope.$index+1) + pageSize * (currentPage-1)}}
                        </template>
                    </el-table-column>
                    <el-table-column align="center" prop="orderNumber" label="订单号">
                    </el-table-column>
                    <el-table-column align="center" prop="userName" label="租客">
                    </el-table-column>
                    <el-table-column align="center" prop="user_owner_name" label="车东">
                    </el-table-column>
                    <el-table-column align="center" prop="carUuid" label="车辆">
                    </el-table-column>
                    <el-table-column align="center" label="使用时间" width="110" :formatter="formatTime">
                    </el-table-column>
                    <el-table-column align="center" prop="useCarTime" label="时长">
                    </el-table-column>
                    <el-table-column align="center" prop="leasePrice" label="租金">
 -->                </el-table-column>
                    <el-table-column align="center" prop="deposit" label="押金">
                    </el-table-column>
                    <el-table-column align="center" prop="takeCarAddress" label="取车地点">
                    </el-table-column>
                    <el-table-column align="center" prop="returnCarAddress" label="还车地点">
                    </el-table-column>
                    <el-table-column align="center" prop="orderStatus" label="状态" :formatter="formatStatus">
                    </el-table-column>
                    <el-table-column align="center" label="操作" width="120">
                        <template scope="scope">
                            <!-- <el-button type="info" size="small" @click="editItem(scope.$index, scope.row)">查看详情</el-button> -->
                            <!-- <el-button type="warning" size="small" @click="transfer(scope.$index, scope.row)">转账</el-button> -->
                            <el-button type="danger" size="small" @click="cancelOrder(scope.$index, scope.row)" :disabled="scope.row.orderStatus == 7? true : false">{{scope.row.orderStatus == 7? '订单已被取消' : '人工取消订单'}}</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <el-row>
            <el-col>
                <div class="block pagination">
                    <el-pagination :current-page.sync="currentPage" @current-change="handleCurrentChange" :page-size="pageSize" layout="prev, pager, next, jumper" :page-count="total">
                    </el-pagination>
                </div>
            </el-col>
        </el-row>
    
        <el-dialog class="edit-dialog dialog-info" title="订单详情" :visible.sync="editDialog">
            <div class="info-content">
                <el-row>
                    <el-col>订单号：<span>{{form.orderNumber}}</span></el-col>
                    <el-col>用户：<span>{{form.userName}}</span></el-col>
                    <el-col>车东：<span>{{form.carOwner}}</span></el-col>
                    <el-col>费用：<span>{{form.totalPrice || 0}} 元</span></el-col>
                    <el-col>取车时间：<span>{{form.takeCarTime}}</span></el-col>
                    <el-col>还车时间：<span>{{form.returnCarTime}}</span></el-col>
                    <el-col>用车时长：<span>{{form.userCarTime}}</span></el-col>
                    <el-col>取车地点：<span>{{form.takeCarAddress}}</span></el-col>
                    <el-col>还车地点：<span>{{form.returnCarAddress}}</span></el-col>
                    <el-col>罚单：{{form.count}}<el-button type="text" v-if="form.count" @click="toTicketPage(form.userName)">　查看</el-button></el-col>
                    <el-col>状态：<span>{{formatStatus(form)}}</span></el-col>
                    <el-col>下单时间：<span>{{form.createAt}}</span></el-col>
                </el-row>
            </div>

            <p class="info-title">费用明细：</p>
            <div class="info-content">
                <el-row>
                    <el-col :span="24">
                        车辆租赁费：<span>{{form.leasePrice || 0}} 元</span>
                    </el-col>
                    <el-col :span="24">
                        基本保险费：<span>{{form.insurancePrice || 0}} 元</span>
                    </el-col>
                    <el-col :span="24">
                        其他费用：<span>{{form.otherPrice || 0}} 元</span>
                    </el-col>
                </el-row>
            </div>
            
            <!-- <div v-if="form.isDamage"> -->
                <p class="info-title">车损照片：</p>
                <el-row class="info-content">
                    <el-col>
                        <img :src="form.damagePicture" alt="">
                    </el-col>
                </el-row>

                <p class="info-title">车损描述</p>
                <el-row class="info-content">
                    <el-col>
                        <p>{{form.damageDsp}}</p>
                    </el-col>
                </el-row>

                <p class="info-title">协调赔偿：</p>
                <div class="info-content">
                    <el-row>
                        <el-col>
                            <p>{{form.compensateMoney || 0}}元</p>
                        </el-col>
                    </el-row>
                </div>
            <!-- </div> -->

            <div slot="footer" class="dialog-footer">
                <el-button type="primary" @click="editDialog = false">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
export default {
    data() {
        return {
            orderWord: '',
            nameWord: '',
            searchOrder: '',
            searchName: '',
            orderBy: 2,
            listLoading: false,
            exportLoading: false,
            tableData: [],

            editDialog: false, //弹出框-编辑
            form: {
                orderNumber: null,
                userName: '',
                useCarTime: '',
                totalPrice: '',
                orderStatus: '',
                createAt: {
                    time: null
                }
            },
            // 分页
            currentPage: 1,
            pageSize: 20,
            total: 0,
            tableTop: 0,
        }
    },
    props: ['API'],
    computed: {
        tableMaxHeight() {
            return (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight || 0) - this.tableTop - 100
        }
    },
    methods: {
        search(ev) {
            this.orderWord = this.searchOrder
            this.nameWord = this.searchName
            this.currentPage = 1
            this.getList(this.nameWord, this.orderWord)
        },
        // 排序
        sortTable() {
            this.orderBy = this.orderBy == 1? 2: 1
            this.getList(this.nameWord, this.orderWord)
        },
        // 格式化
            // 格式化时间
            formatTime(row, column) {
                return this.$common.getNowTime(row.takeCarTime && row.takeCarTime.time)
            },
            getTime(time) {
                return this.$common.getNowTime(time)
            },
            // 格式化状态
            formatStatus(row, column) {
                switch (row.orderStatus) {
                    case 1: return '未接单'
                    case 2: return '拒绝接单'
                    case 3: return '已接单'
                    case 4: return '已接单'
                    case 5: return '确认还车未收车'
                    case 6: return '已收车'
                    case 7: return '已取消'
                    case 8: return '已退款'
                    default: return '未知'
                }
            },
        editItem(index, row) {
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "order_uuid": row.orderUuid
                }
            }
            this.$common._post('order/detail', json, this, data => {
                this.editDialog = true
                this.form = Object.assign({}, data)
            })
        },
        exportExc() {
            this.exportLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": { }
            }
            this.$http.post(this.API + 'exportExcelOrder', json).then(data=>{
                this.exportLoading = false
                var data = data.body
                if (data.body.result == '0') {
                    location.href = data.body.fileUrl
                } else {
                    this.$message(data.body.description)
                }
            })
        },
        // 取消订单
        cancelOrder(index, row) {
            this.$confirm('是否确认取消订单?', '提示', {
              type: 'warning'
            }).then(() => {
                var json = {
                    "head": {
                        "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                        "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                    },
                    "body": {
                       "order_uuid": row.orderUuid,
                    }
                }
                this.$common._post('order/cancelOrder', json, this, data => {
                    this.$message({ type: 'success',message: data.description })
                    this.getList()
                })
            }).catch(() => {})
        },
        // 转账
        transfer(index, row) {
            this.$prompt('请输入转账金额：', '转账', {
                inputPattern: /^([1-9]\d{0,8}\.\d{1,2}|0\.\d{1,2}|[1-9]\d{0,8})$/,
                inputErrorMessage: '金额格式不正确'
            }).then(( {value} ) => {
                this.$confirm(`是否确认转账 ${Math.floor(value*100)/100} 元?`, '提示', {
                  type: 'warning'
                }).then(() => {
                    var json = {
                        "head": {
                            "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                            "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                        },
                        "body": {
                           "order_uuid": row.orderUuid,
                           "money": value
                        }
                    }
                    this.$common._post('order/transferOrder', json, this, data => {
                        this.$message({ type: 'success',message: data.description })
                        this.getList()
                    })
                }).catch(() => {})
            }).catch(() => {})
        },
        //跳转到罚单页面
        toTicketPage(name) {
            this.$router.push('/ticket-info')
            this.$store.commit('setTicketName', name)
        },
        handleCurrentChange(val) {
            this.currentPage = val
            this.getList(this.nameWord, this.orderWord)
        },
        // 获取列表
        getList(name = '', order = ''){
            this.listLoading = true
            var json = {
                "head": {
                    "account": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userName,
                    "password": JSON.parse(sessionStorage.getItem('zhizunbao_login')).userPassword
                },
                "body": {
                   "pageIndex": this.currentPage,
                   "pageSize": this.pageSize,
                   "orderNumber": order,
                   "userName": name,
                   "orderBy": this.orderBy,
                }
            }
            this.$common._post('order/list', json, this, data => {
                this.total = data.total
                this.tableData = data.orderList
                this.listLoading = false
            }, data=>{
                this.listLoading = false
                this.tableData = []
            })
        }
    },
    mounted(){
        if (!sessionStorage.getItem('zhizunbao_login')) {
            this.$message('请登录账号')
            this.$router.push('/')
            return
        }
        // 从平台账户过来的搜索
        if (this.$store.state.orderNumber) {
            this.searchOrder = this.$store.state.orderNumber
            this.orderWord = this.searchOrder
        }
        this.getList('', this.orderWord)
        
        // 表格距离顶部的高度
        this.tableTop = (document.querySelector('#main table').getBoundingClientRect().top)
    }
}
</script>

<style scoped lang="scss">

</style>



